<template lang="pug">
  div(class="login-root")
    div
      div(class="logo-font")
      div
        form(@submit.prevent="login")
          div
            div
              input(type="email" class="input-default" placeholder="Email" v-model="email")
            div
              input(type="password" class="input-default" placeholder="密碼" v-model="password")
            button(type='submit' class="button-default") 登入
            div
              router-link(tag="span" to="/register") 尚未擁有帳號？註冊新帳號
        div
          p - 其他方式 -
          button(type="button") 以 Facebook 帳號繼續
          button(type="button") 以 Google 帳號繼續
</template>

<script>
export default {
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    async login (e) {
      e.preventDefault()
      try {
        const result = await this.$store.dispatch('user/login', { email: this.email, password: this.password })

        if (!result) {
        }

        this.$router.push('/home')
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.login-root {
  width: 100%;
  height: 100%;
  background-color: black;
  position: absolute;
  z-index: 10;
  > div:nth-child(1) {
    width: 100%;
    height: 100%;
    background-image: url("~@/assets/image/startPage/login-bg.png");
    background-position: center;
    background-size: cover;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 154px 513px;
    > div:nth-child(2) {
      box-sizing: border-box;
      justify-self: center;
      width: 324px;
      border: 1px solid map-get($colors, white);
      border-bottom: none;
      border-top-left-radius: 7.5px;
      border-top-right-radius: 7.5px;
      background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
      box-shadow: map-get($shadow, secondary-4);
      padding: 0 24px;

      display: grid;
      grid-template-rows: 272px 1fr;
      grid-row-gap: 47px;
      > form:nth-child(1) {
        > div {
          display: grid;
          grid-template-rows: 50px 50px 19px 50px 37px 32px 19px 15px;
          grid-template-areas:
            "."
            "email"
            "."
            "password"
            "."
            "login"
            "."
            "register";
          > div:nth-child(1) {
            grid-area: email;
          }
          > div:nth-child(2) {
            grid-area: password;
          }
          > button:nth-child(3) {
            justify-self: center;
            grid-area: login;
            width: 116px;
            height: 32px;
            font-size: 14px;
            font-weight: 900;
          }
          > div:nth-child(4) {
            justify-self: center;
            grid-area: register;
            width: 132px;
            border-bottom: 1px solid map-get($colors, white);
            > span {
              display: block;
              font-size: 11px;
              line-height: 11px;
              padding-bottom: 4px;
              text-shadow: map-get($shadow, secondary-4);
            }
          }
        }
      }
      > div:nth-child(2) {
        display: grid;
        grid-template-rows: 12px 36px 36px;
        grid-row-gap: 16px;
        > p:nth-child(1) {
          font-size: 12px;
          text-shadow: map-get($shadow, secondary-4);
        }
        > button {
          border-radius: 4.5px;
          border: none;
          outline: none;
          background-color: #4267b2;
          font-size: 12px;
          font-weight: 900;
        }
        > button:nth-child(3) {
          background-color: #dd4b39;
        }
      }
    }
  }
}
</style>
